import React, { useState } from 'react';

const JobsManagementSection = ({ 
  jobs, 
  onAddJob, 
  onEditJob, 
  onToggleJobStatus, 
  onViewCandidates,
  recruiterId,
  onCreateJob 
}) => {
  const handleCreateJob = () => {
    onCreateJob();
  };

  const handleEditJob = (jobId) => {
    onEditJob(jobId);
  };

  const handleToggleStatus = (jobId) => {
    onToggleJobStatus(jobId);
  };

  const handleViewCandidates = (jobTitle) => {
    onViewCandidates(jobTitle);
  };

  return (
    <div className="section-card">
      <div className="section-header">
        <h3 className="section-title">My Job Postings</h3>
        <button className="edit-btn" onClick={handleCreateJob}>
          <i className="fas fa-plus"></i> Post New Job
        </button>
      </div>
      
      <div id="projectsDisplay">
        {jobs.map((job, index) => (
          <div key={job.id || index} className="project-item" data-job-id={job.id || index}>
            <div className="project-header">
              <div className="project-title">{job.title}</div>
              <button 
                className="edit-job-btn" 
                onClick={() => handleEditJob(job.id || index)} 
                title="Edit job posting"
              >
                <i className="fas fa-edit"></i>
              </button>
            </div>
            <div className="project-description">{job.description}</div>
            <div className="project-links">
              <button 
                className={`job-status ${job.status.toLowerCase()}`} 
                onClick={() => handleToggleStatus(job.id || index)}
              >
                <i className={`fas ${job.status === 'Active' ? 'fa-circle' : 'fa-times-circle'}`}></i> 
                {job.status}
              </button>
              <span className="job-applications">
                <i className="fas fa-users"></i> {job.applications} applications
              </span>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default JobsManagementSection;
