import React, { useState, useEffect } from 'react';
import RecruiterHeader from './RecruiterHeader';
import RecruiterProfileCard from './RecruiterProfileCard';
import JobsManagementSection from './JobsManagementSection';
import RecruiterJobOffersSection from './RecruiterJobOffersSection';
import RecruiterPersonalInfoSection from './RecruiterPersonalInfoSection';
import CreateJobPage from './CreateJobPage';
import { recruiterAPI } from '../services/apiService';
import '../recruiter.css';

const RecruiterPortal = ({ onLogout, onOpenJobs, onGoToHome }) => {
  // Profile data state
  const [profileData, setProfileData] = useState({
    name: '',
    title: '',
    email: '',
    phone: '',
    company: '',
    jobs: [],
    cvFile: null,
    avatarFile: null,
    coverFile: null
  });

  // Loading and error states
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [recruiterId, setRecruiterId] = useState(null);

  // State for job offers
  const [jobOffers, setJobOffers] = useState([]);

  // Auto-save functionality
  const [autoSaveStatus, setAutoSaveStatus] = useState('');

  // State for create job page
  const [showCreateJobPage, setShowCreateJobPage] = useState(() => {
    // Check if user was on create job page before refresh
    return localStorage.getItem('isOnCreateJobPage') === 'true';
  });

  useEffect(() => {
    // Load recruiter data from database
    const loadRecruiterData = async () => {
      try {
        setIsLoading(true);
        setError(null);

        // Get recruiter ID from localStorage (set during login)
        const savedRecruiterId = localStorage.getItem('recruiterId');
        if (!savedRecruiterId) {
          throw new Error('No recruiter ID found. Please log in again.');
        }

        setRecruiterId(savedRecruiterId);

        // Load recruiter details
        const detailsResult = await recruiterAPI.getDetails(savedRecruiterId);
        if (!detailsResult.success) {
          throw new Error(detailsResult.message || 'Failed to load recruiter details');
        }

        const recruiterData = detailsResult.data;
        if (!recruiterData) {
          throw new Error('Recruiter data not found');
        }

        // Load recruiter jobs
        const jobsResult = await recruiterAPI.getMyJobs(savedRecruiterId);
        let jobs = [];
        if (jobsResult.success && jobsResult.data) {
          jobs = jobsResult.data.map(job => ({
            id: job.jobId,
            title: job.title,
            company: job.company,
            description: job.description,
            status: job.status,
            applications: 0 // Will be loaded separately if needed
          }));
        }

        // Update profile data with real database data
        setProfileData({
          name: recruiterData.fullName || `${recruiterData.firstName || ''} ${recruiterData.lastName || ''}`.trim(),
          title: recruiterData.company || '',
          email: recruiterData.email || '',
          phone: recruiterData.phoneNumber || '',
          company: recruiterData.company || '',
          jobs: jobs,
          cvFile: null,
          avatarFile: null,
          coverFile: null
        });

      } catch (error) {
        console.error('Failed to load recruiter data:', error);
        setError(error.message);
      } finally {
        setIsLoading(false);
      }
    };

    loadRecruiterData();
  }, []);

  // Clean up create job page state when component unmounts
  useEffect(() => {
    return () => {
      // Only clean up if we're actually leaving the portal
      if (!showCreateJobPage) {
        localStorage.removeItem('isOnCreateJobPage');
      }
    };
  }, [showCreateJobPage]);

  // Auto-save function (disabled for now - will implement when backend supports updates)
  const autoSave = () => {
    // TODO: Implement database save when backend supports recruiter updates
    setAutoSaveStatus('saved');
    setTimeout(() => setAutoSaveStatus(''), 2000);
  };

  // Debounced auto-save (disabled for now)
  // useEffect(() => {
  //   const timeoutId = setTimeout(autoSave, 2000);
  //   return () => clearTimeout(timeoutId);
  // }, [profileData]);

  // Event handlers
  const handleRefreshProfile = async () => {
    console.log('Refreshing profile...');
    setAutoSaveStatus('saving');
    
    try {
      if (!recruiterId) {
        throw new Error('No recruiter ID available');
      }

      // Reload recruiter details
      const detailsResult = await recruiterAPI.getDetails(recruiterId);
      if (!detailsResult.success) {
        throw new Error(detailsResult.message || 'Failed to load recruiter details');
      }

      const recruiterData = detailsResult.data;
      if (!recruiterData) {
        throw new Error('Recruiter data not found');
      }

      // Reload recruiter jobs
      const jobsResult = await recruiterAPI.getMyJobs(recruiterId);
      let jobs = [];
      if (jobsResult.success && jobsResult.data) {
        jobs = jobsResult.data.map(job => ({
          id: job.jobId,
          title: job.title,
          company: job.company,
          description: job.description,
          status: job.status,
          applications: 0
        }));
      }

      // Update profile data
      setProfileData(prev => ({
        ...prev,
        name: recruiterData.fullName || `${recruiterData.firstName || ''} ${recruiterData.lastName || ''}`.trim(),
        title: recruiterData.company || '',
        email: recruiterData.email || '',
        phone: recruiterData.phoneNumber || '',
        company: recruiterData.company || '',
        jobs: jobs
      }));

      setAutoSaveStatus('saved');
      setTimeout(() => setAutoSaveStatus(''), 2000);
    } catch (error) {
      console.error('Failed to refresh profile:', error);
      setError(error.message);
      setAutoSaveStatus('');
    }
  };


  const handleLogout = () => {
    onLogout();
  };

  const handleAvatarUpload = (file) => {
    console.log('Avatar uploaded:', file.name);
    setProfileData(prev => ({
      ...prev,
      avatarFile: file
    }));
    setAutoSaveStatus('saved');
    setTimeout(() => setAutoSaveStatus(''), 2000);
  };

  const handleCoverUpload = (file) => {
    console.log('Cover uploaded:', file.name);
    setProfileData(prev => ({
      ...prev,
      coverFile: file
    }));
    setAutoSaveStatus('saved');
    setTimeout(() => setAutoSaveStatus(''), 2000);
  };

  const handleAddJob = async (jobData) => {
    try {
      setAutoSaveStatus('saving');
      
      // Create job via API
      const result = await recruiterAPI.createJob(jobData);
      
      if (result.success) {
        // Refresh jobs list from server
        const jobsResult = await recruiterAPI.getMyJobs(recruiterId);
        if (jobsResult.success && jobsResult.data) {
          const jobs = jobsResult.data.map(job => ({
            id: job.jobId,
            title: job.title,
            company: job.company,
            description: job.description,
            status: job.status,
            applications: 0
          }));
          
          setProfileData(prev => ({
            ...prev,
            jobs: jobs
          }));
        }
        
        setAutoSaveStatus('saved');
        setTimeout(() => setAutoSaveStatus(''), 2000);
      } else {
        setError(result.message || 'Failed to create job');
        setAutoSaveStatus('');
      }
    } catch (error) {
      console.error('Failed to create job:', error);
      setError('Failed to create job');
      setAutoSaveStatus('');
    }
  };

  // Handle successful job creation from CreateJobPage
  const handleJobCreated = () => {
    setShowCreateJobPage(false);
    localStorage.removeItem('isOnCreateJobPage');
  };

  const handleEditJob = (jobId) => {
    console.log('Editing job:', jobId);
    // Here you would typically open a modal or navigate to edit page
    alert(`Edit job functionality for job ID: ${jobId}`);
  };

  const handleToggleJobStatus = (jobId) => {
    setProfileData(prev => ({
      ...prev,
      jobs: prev.jobs.map(job => 
        job.id === jobId 
          ? { ...job, status: job.status === 'Active' ? 'Closed' : 'Active' }
          : job
      )
    }));
    
    setAutoSaveStatus('saved');
    setTimeout(() => setAutoSaveStatus(''), 2000);
  };

  const handleViewCandidates = (jobTitle) => {
    console.log('Viewing candidates for:', jobTitle);
    alert(`View candidates functionality for: ${jobTitle}`);
  };

  const handleSavePersonalInfo = (updatedInfo) => {
    setProfileData(prev => ({
      ...prev,
      ...updatedInfo
    }));
    
    setAutoSaveStatus('saved');
    setTimeout(() => setAutoSaveStatus(''), 2000);
  };

  // Job Offers handlers
  const handleRefreshOffers = () => {
    console.log('Refreshing job offers...');
    // TODO: Implement API call to fetch job offers
  };

  const handleViewOffer = (offerId) => {
    console.log('Viewing offer:', offerId);
  };

  const handleEditOffer = (offerId) => {
    console.log('Editing offer:', offerId);
  };

  const handleDeleteOffer = (offerId) => {
    console.log('Deleting offer:', offerId);
    setJobOffers(prev => prev.filter(offer => offer.id !== offerId));
    setAutoSaveStatus('saved');
    setTimeout(() => setAutoSaveStatus(''), 2000);
  };

  // Create job page handlers
  const handleCreateJob = () => {
    // Navigate to create job page instead of showing it in portal
    window.location.href = `${window.location.origin}${window.location.pathname}?page=create-job&user=recruiter&id=${recruiterId}`;
  };

  const handleBackFromCreateJob = () => {
    setShowCreateJobPage(false);
    localStorage.removeItem('isOnCreateJobPage');
  };

  // Show create job page
  if (showCreateJobPage) {
    return (
      <CreateJobPage
        onBack={handleBackFromCreateJob}
        onJobCreated={handleJobCreated}
        recruiterId={recruiterId}
      />
    );
  }

  // Show loading state
  if (isLoading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner">
          <i className="fas fa-spinner fa-spin"></i>
          <p>Loading recruiter data...</p>
        </div>
      </div>
    );
  }

  // Show error state
  if (error) {
    return (
      <div className="error-container">
        <div className="error-message">
          <i className="fas fa-exclamation-triangle"></i>
          <h3>Error Loading Data</h3>
          <p>{error}</p>
          <button 
            className="btn btn-primary" 
            onClick={() => window.location.reload()}
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div>
      {/* Skip Links for Accessibility */}
      <a href="#main-content" className="skip-link">Skip to main content</a>
      <a href="#profile-card" className="skip-link">Skip to profile</a>
      <a href="#navigation" className="skip-link">Skip to navigation</a>
      
      <RecruiterHeader 
        onRefreshProfile={handleRefreshProfile}
        onLogout={handleLogout}
        onOpenJobs={onOpenJobs}
        onGoToHome={onGoToHome}
        recruiterName={profileData.name}
      />

      <div className="container">
        <RecruiterProfileCard 
          profileData={profileData}
          onAvatarUpload={handleAvatarUpload}
          onCoverUpload={handleCoverUpload}
          onRefreshProfile={handleRefreshProfile}
        />

        <div className="main-content" id="main-content">
          <JobsManagementSection 
            jobs={profileData.jobs}
            onAddJob={handleAddJob}
            onEditJob={handleEditJob}
            onToggleJobStatus={handleToggleJobStatus}
            onViewCandidates={handleViewCandidates}
            recruiterId={recruiterId}
            onCreateJob={handleCreateJob}
          />

          <RecruiterPersonalInfoSection 
            personalInfo={profileData}
            onSavePersonalInfo={handleSavePersonalInfo}
          />

          <RecruiterJobOffersSection 
            jobOffers={jobOffers}
            onRefreshOffers={handleRefreshOffers}
            onViewOffer={handleViewOffer}
            onEditOffer={handleEditOffer}
            onDeleteOffer={handleDeleteOffer}
          />
        </div>
      </div>

      {/* Auto-save Indicator */}
      {autoSaveStatus && (
        <div className={`auto-save-indicator show ${autoSaveStatus}`}>
          <i className="fas fa-save"></i>
          <span>
            {autoSaveStatus === 'saving' && 'Auto-saving...'}
            {autoSaveStatus === 'saved' && 'Changes saved'}
          </span>
        </div>
      )}
    </div>
  );
};

export default RecruiterPortal;
