import React from 'react';

const JobCard = ({ job, onApplyToJob, index }) => {
  const handleApply = () => {
    onApplyToJob(job.id);
  };

  return (
    <div 
      className="job-card fade-in-up" 
      style={{ animationDelay: `${index * 0.1}s` }}
    >
      <div className="job-header">
        <div>
          <h3 className="job-title">{job.title}</h3>
          <p className="job-company">{job.company}</p>
        </div>
        <span className="job-type-badge">{job.type}</span>
      </div>
      
      <div className="job-details">
        <div className="job-detail">
          <i className="fas fa-map-marker-alt"></i>
          <span>{job.location}</span>
        </div>
        <div className="job-detail">
          <i className="fas fa-home"></i>
          <span>Remote: {job.remote}</span>
        </div>
        <div className="job-detail">
          <i className="fas fa-tag"></i>
          <span>Field: {job.field}</span>
        </div>
        <div className="job-detail">
          <i className="fas fa-briefcase"></i>
          <span>Experience: {job.experience}</span>
        </div>
        <div className="job-detail">
          <i className="fas fa-clock"></i>
          <span>Job Type: {job.type}</span>
        </div>
      </div>
      
      <div className="job-description">
        {job.description}
      </div>
      
      <div className="job-requirements">
        <div className="requirements-title">Requirements:</div>
        <div className="requirements-text">{job.requirements}</div>
      </div>
      
      <div className="job-actions">
        <button className="btn btn-primary" onClick={handleApply}>
          <i className="fas fa-paper-plane"></i> Apply Now
        </button>
      </div>
    </div>
  );
};

export default JobCard;
