import React from 'react';
import '../recruiter-jobs.css';

const PublicJobCard = ({ job, hideApplyButton = false, onSignInClick }) => {
  const getRemoteIcon = (remote) => {
    switch (remote) {
      case 'Yes':
        return <i className="fas fa-home"></i>;
      case 'No':
        return <i className="fas fa-building"></i>;
      case 'Hybrid':
        return <i className="fas fa-laptop-house"></i>;
      default:
        return <i className="fas fa-map-marker-alt"></i>;
    }
  };

  const getTypeColor = (type) => {
    switch (type) {
      case 'Full-time':
        return '#28a745';
      case 'Part-time':
        return '#ffc107';
      case 'Contract':
        return '#17a2b8';
      case 'Internship':
        return '#6f42c1';
      default:
        return '#6c757d';
    }
  };

  return (
    <div className="job-card">
      <div className="job-header">
        <div className="job-title-section">
          <h3 className="job-title">{job.title}</h3>
          <p className="job-company">{job.company}</p>
        </div>
        <div className="job-type-badge" style={{ backgroundColor: getTypeColor(job.type) }}>
          {job.type}
        </div>
      </div>

      <div className="job-details">
        <div className="job-location">
          <i className="fas fa-map-marker-alt"></i>
          <span>{job.location}</span>
        </div>
        <div className="job-remote">
          {getRemoteIcon(job.remote)}
          <span>{job.remote === 'Yes' ? 'Remote' : job.remote === 'No' ? 'On-site' : 'Hybrid'}</span>
        </div>
        <div className="job-field">
          <i className="fas fa-tag"></i>
          <span>{job.field}</span>
        </div>
      </div>

      <div className="job-description">
        <p>{job.description}</p>
      </div>

      <div className="job-requirements">
        <h4>Requirements:</h4>
        <p>{job.requirements}</p>
      </div>

      <div className="job-footer">
        {!hideApplyButton && (
          <div className="job-actions">
            <button 
              className="btn btn-primary" 
              onClick={onSignInClick}
              style={{ 
                cursor: 'pointer'
              }}
            >
              <i className="fas fa-sign-in-alt"></i>
              Sign in to Apply
            </button>
          </div>
        )}
        <div className="job-meta">
          <span className="job-id">Job ID: #{job.id}</span>
        </div>
      </div>
    </div>
  );
};

export default PublicJobCard;
